  <el-menu-item index="4">切诊      </el-menu-item>
  <el-menu-item index="5">咨询      </el-menu-item>
  <el-menu-item index="6">健康知识      </el-menu-item>
  <el-menu-item index="7">联系我们      </el-menu-item>
</el-menu>

    </div>
</template>
<script>
export default{
    data() {
        return {
        activeIndex2: '1'
        }
    },    methods: {
        gotoreg(){
          this.$router.push('/message')
        },
        gotosearch(){
            this.$router.push('/search')
        }
    },
}
</script>
